@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');

* {
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', 'sans-serif';
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  min-height: 100vh;
  background-color: #eec0c6;
  background-image: linear-gradient(315deg, #eec0c6 0%, #7ee8fa 74%);
}

.container {
  background-color: #fff;
  border-radius: 10px;
  width: 400px;
  max-width: 100%;
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}

.quiz-header {
  padding: 30px;
}

h2 {
  padding: 1rem;
  text-align: center;
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
}

ul li {
  cursor: pointer;
  font-size: 1.1rem;
  margin: 1rem 0;
}

ul li label {
  cursor: pointer;
}

button {
  background-color: aquamarine;
  display: block;
  color: white;
  font-family: inherit;
  border: none;
  cursor: pointer;
  font-size: 1.1rem;
  width: 100%;
  padding: 1.3rem;
}

button:hover {
  background-color: rgb(129, 238, 202);
}

button:focus {
  background-color: rgb(129, 238, 202);
  outline: none;
}
